import styled from 'styled-components'
import { useActions, useStore } from 'statux'
import React, { useState } from 'react'
import store from '../../store'
import Button from '../core/Button'
import { isEnterKey } from '../../lib/keyboard'

const SearchPlayer = styled.input`
  border: 1px solid ${(props) => props.theme.colors.button};
  background: white;
  color: #243949;
  display: inline-block;
  padding: 5px 15px;
`

const Container = styled.div`
  display: flex;
  height: ${(props) => props.theme.size.M};
`

export default function FindPlayer() {
  const setSearch = useActions(store.search)
  const [pagination, setPagination] = useStore(store.pagination)
  const [name, setName] = useState('')

  function onChange(e) {
    setName(e.target.value)
  }

  function onClick() {
    setPagination({ ...pagination, rank: 0 })
    setSearch(name)
  }

  function onKeyDown(event) {
    if (isEnterKey(event)) {
      onClick()
    }
  }

  return (
    <Container>
      <SearchPlayer
        type={'text'}
        placeholder={'Player name'}
        onChange={onChange}
        onKeyDown={onKeyDown}
      />
      <Button onClick={onClick}>Find</Button>
    </Container>
  )
}
